/* Big tablet to 1200px (widths smaller taht the 1140px row) */
@media only screen and (max-width: 1300px) {
    html {
        font-size: 53%;
    }
}

/* Small tablet to big tablet: from 768px to 1023px */
@media only screen and (max-width: 1023px) {
    html {
        font-size: 53%;
    }

    .Content_container {
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
        -webkit-box-pack: center;
            -ms-flex-pack: center;
                justify-content: center;

        .Content {
            display: -webkit-box;
            display: -ms-flexbox;
            display: flex;
            -webkit-box-orient: vertical;
            -webkit-box-direction: normal;
                -ms-flex-direction: column;
                    flex-direction: column;
            -webkit-box-pack: center;
                -ms-flex-pack: center;
                    justify-content: center;
            -ms-flex-line-pack: center;
                align-content: center;
            width: 99%;
            display: flex;
            justify-content: center;
            -ms-flex-wrap: wrap;
                flex-wrap: wrap;
            align-content: center;
            -webkit-animation: slide-down 0.9s ease;
                    animation: slide-down 0.9s ease;
            font-size: 1.8rem;
            padding: 0;

            .pic {
                width: 100%;
                margin: 0 0.8rem;

                img {
                    width: 100%;
                    border-radius: 10px;
                }
            }

            .text {
                width: 100%;
                padding: 1rem;
                background-color: rgba(255, 255, 255, 0.1);
                border-radius: 3px;
                display: -webkit-box;
                display: -ms-flexbox;
                display: flex;
                -webkit-box-align: center;
                    -ms-flex-align: center;
                        align-items: center;
                -webkit-box-pack: center;
                    -ms-flex-pack: center;
                        justify-content: center;

                .inline-text {
                    @include colorGradient(700);
                }
            }
        }
    }
}

/* Small phones to small tablets: from 481px to 767px */
@media only screen and (max-width: 767px) {
    * {
        cursor: default;
    }
    html {
        font-size: 45%;
    }
    .Shelf {
        cursor: default;
    }

    .info {
        bottom: 4rem;
        right: 0.5rem;
        font-size: 1.6rem;
    }
}

/* Small phones: from 0 to 480px */
@media only screen and (max-width: 480px) {
    * {
        cursor: default;
    }
    .ShelfList {
        width: 65%;
        padding: 10px;
    }

    .info {
        bottom: 4rem;
        right: 0.5rem;
        text-align: start;
        font-size: 1.3rem;
    }
}
