.Content_container {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;

    .Content {
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
        -webkit-box-pack: center;
            -ms-flex-pack: center;
                justify-content: center;
        -ms-flex-line-pack: center;
            align-content: center;
        width: 99%;
        display: flex;
        justify-content: center;
        -ms-flex-wrap: wrap;
            flex-wrap: wrap;
        align-content: center;
        -webkit-animation: slide-down 0.9s ease;
                animation: slide-down 0.9s ease;
        font-size: 1.8rem;
        padding: 0;

        .pic {
            width: 62%;
            margin: 0 0.8rem;

            img {
                width: 100%;
                border-radius: 10px;
            }
        }

        .text {
            width: 35%;
            padding: 1rem;
            background-color: rgba(255, 255, 255, 0.1);
            border-radius: 3px;
            display: -webkit-box;
            display: -ms-flexbox;
            display: flex;
            -webkit-box-align: center;
                -ms-flex-align: center;
                    align-items: center;
            -webkit-box-pack: center;
                -ms-flex-pack: center;
                    justify-content: center;

            .inline-text {
                @include colorGradient(700);
            }
        }
    }
}
